* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
}
.container {
  display: flex;
  max-width: 1200px;
  margin: 20px auto;
}
.sidebar {
  position: sticky;
  top: 20px;
  width: 25%;
  background: #f4f4f4;
  padding: 15px;
  border-radius: 5px;
}
.content {
  width: 75%;
  padding: 20px;
}
.tabs {
  display: flex;
  margin-bottom: 10px;
}
.tab {
  flex: 1;
  padding: 10px;
  cursor: pointer;
  background: #ddd;
  border: 1px solid #ccc;
  text-align: center;
}
.tab.active {
  background: #007bff;
  color: #fff;
}
.tab-content {
  background: #f9f9f9;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 10px;
}
img {
  max-width: 100%;
  border-radius: 5px;
  margin: 15px 0;
}
.accordion {
  margin: 15px 0;
}
.accordion-btn {
  width: 100%;
  text-align: left;
  padding: 10px;
  cursor: pointer;
  background: #007bff;
  color: white;
  border: none;
  margin: 5px 0;
}
.accordion-btn.active {
  background: #0056b3;
}
.accordion-content {
  padding: 10px;
  background: #f1f1f1;
}
table {
  width: 100%;
  border-collapse: collapse;
  margin: 15px 0;
}
table th, table td {
  border: 1px solid #ddd;
  padding: 10px;
}
table th {
  background: #007bff;
  color: white;
}
.cta-button {
  position: sticky;
  bottom: 20px;
  display: block;
  background: #28a745;
  color: white;
  padding: 10px 15px;
  text-align: center;
  border-radius: 5px;
  text-decoration: none;
}


